.firstcontainer {
    box-shadow: 0 0 10px rgb(109, 108, 108);
    padding: 30px 20px 30px 20px;
    height: 560px;
    border-radius: 15px;
}

.heading1 {
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 0;
    padding-top: 10px;
}

.input input {
    margin-right: 5%;
}

@media screen and (max-width:500px) {
    .firstcontainer {
        width: 130%;
        margin-left: -20px;
        padding: 5px;
        margin: 0;
    }
    .heading1 {
        font-size: 13px;
    }
}

@media screen and (max-width:377px) {
    .firstcontainer {
        width: 130%;
        height: 100%;
        margin-left: -15px;
    }
    .heading1 {
        font-size: 17px;
        margin: 0;
    }
}